	module.exports = {"data":{"markdownRemark":{"html":"<p>凌晨读《纪实与虚构》，是一部小说，好像是在写作者的经历，或者说一种发展了的自己的经历。还没有读完，现在还不能下结论。里面讲到了一种中国社会在1949年以后特有的文化现象，或者说城市社会生活中特有的东西。就是解放军进城以后，在社会中形成了两种不同文化，一种是延安铸造出来的革命文化，在小说里体现为\"同志\"文化，另一种是城市里本来就有的文化，所谓的\"小市民\"文化。</p>\n<p>这本小说以这个冲突或者差距来展开情节。不过这两种文化在现代城市里确实存在了一阵子，对人们的社会生活以至政治生活、心理结构产生了不可低估的影响。研究当代中国社会的社会发展不能忽视这一点。近来，市场经济和商品经济的发展，使文化中的这种差异越来越小，商品文化有巨大的力量，能够突破一切文化上的壁垒。</p>","id":"/home/swain/workspace/political-life/src/pages/zh/0025.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/zh/0025.md","frontmatter":{"title":"2月5日 星期六"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/zh/0025.md absPath of file >>> MarkdownRemark"}}

